import React, { useContext } from 'react'
import { Container, Box } from '@mui/material'
import { SimpleCard } from '../Cards'
import { GlobalContext } from '../context/Context'

export function VideoAndPhotoSection() {
    const { cards_container_style } = useContext(GlobalContext)


    return (
        <Box sx={{
            padding: '5rem 0 5rem 0',
        }}>
            <Container>
                <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <h1>Produccin de Video y Fotografa</h1>
                    <p>En NC te ofrecemos producciones audiovisuales de la ms alta calidad gracias a nuestra tecnologa de ltima generacin. Ofrecemos una amplia variedad de soluciones en fotografa y video para cada necesidad.</p>
                </div>
            </Container>

            <Container sx={cards_container_style}>
                <div data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <SimpleCard title='Videos corporativos' content='Videos corporativos con los ms altos estndares de produccin.' background_color='transparent' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="400" data-aos-duration="1000">
                    <SimpleCard title='Fotografa de Productos' content='Fotografa profesional de productos para catlogos impresos o digitales.' background_color='transparent' />
                </div>
                <div data-aos="zoom-in" data-aos-delay="600" data-aos-duration="1000">
                    <SimpleCard title='Eventos de marca' content='Registro cinematogrfico y fotogrfico de todo tipo de eventos para marcas de empresas.' background_color='transparent' />
                </div>
            </Container>
        </Box>
    )
}
